import React,{useState} from "react";
import './contact.css'
export const Contact = () => {

     
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');
  const [showPopup, setShowPopup] = useState(false);
  
  const handleSubmit = async (e) => {
      e.preventDefault();
       
      try {
          const response = await fetch('https://genesisinter.info/mail.php', {
              method: 'post',
              mode: 'cors',
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded',
              },
              body: `name=${name}&email=${email}&message=${message}`,
          });
          
          if (response.ok) {
              // Handle success
              setShowPopup(true);
              clearFormFields();
              console.log('Email sent successfully');
          } else {
              // Handle error
              console.error('Failed to send email');
          }
      } catch (error) {
          console.error('Error:', error);
      }
  };
  
  const clearFormFields = () => {
    setName('');
    setEmail('');
    setMessage('');
};

  return (
    <div className="contact-section">
      <div className="conatct-heading text-center p-5 ">
        <h1 className="fw-bold text-white">Contact Us</h1>
      </div>
      <div class="container">
        <div class="content">
          <div class="left-side">
            <div class="address details">
              <i class="fas fa-map-marker-alt"></i>
              <div class="topi  fw-bold">Address</div>
              <div class="text-one text-dark">
                {/* 55 Village Center PI,Missisauga */}
              </div>
              <div class="text-two text-dark"></div>
            </div>
            <div class="phone details">
              <i class="fas fa-phone-alt"></i>
              <div class="topic fw-bold">Phone</div>
              <div class="text-one text-dark"></div>
            </div>
            <div class="email details">
              <i class="fas fa-envelope"></i>
              <div class="topic fw-bold">Email</div>
              <div class="text-one text-dark"></div>
              <div class="text-two text-dark"></div>
            </div>
          </div>
          <div class="right-side">
            <p>
              If you have any work from me or any types of quries related to my,
              you can send me message from here. It's my pleasure to help you.
            </p>
            <form onSubmit={handleSubmit} >
              <div class="input-box">
                <input
                  type="text"
                  placeholder="Enter your name"
                  value={name} onChange={(e) => setName(e.target.value)}
                />
              </div>
              <div class="input-box">
                <input
                  type="email"
                  name="email"
                  placeholder="Enter your email"
                  value={email} onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div class="input-box message-box">
                <textarea
                  name="message"
                  id="message"
                  placeholder="Enter your message"
                 value={message} onChange={(e) => setMessage(e.target.value)}
                ></textarea>
              </div>
              <div class="button">
                <input type="submit"  value="Send Now" />
              </div>
            </form>
          </div>
        </div>
      </div>
      {showPopup && (
                <div className="popup">
                    <div className="popup-content">
                        <p>Message sent successfully!</p>
                        <button className="px-3 py-2 bg-dark text-white" onClick={() => setShowPopup(false)}>Close</button>
                    </div>
                </div>
            )}
    </div>
  );
};

