import React from 'react'
import { Image } from "react-bootstrap";
import { Button } from "react-bootstrap";

import { HiOutlineShoppingCart } from "react-icons/hi";
// import { useDispatch, useSelector } from "react-redux";

export const ProductCard = ({ product, index }) => {
  return (
    <div>
        <div className="border border-seconadary rounded">
      <Image
        src={product.img}
        alt={product.name}
        width="100%"
        className="rounded"
        fluid
        role="button" 
        key={index}/>
      <div class="px-3 text-left">
        <div role="button">
          <div className="px-0  pt-3 bg-red" style={{ textAlign: "initial" }}>
            <p
              to="#"
              className="text-decoration-none text-left fs-6 mb-0"
              style={{ color: "#056cb8" }}
              rel="tag"
            >
              {product.category}
            </p>
          </div>
          <div className="text-left" style={{ textAlign: "initial" }}>
            <p
              className="text-decoration-none text-dark text-center fw-bold fs-5 py-3 font my-0"
              to="/"
            >
              {product.name}
            </p>
          </div>
        </div>
        <div className="d-flex justify-content-between">
       
          {/* <Button
            variant="transparent"
            onClick={() => send(product)}
            className="btn-cart pb-3"
          >
            <HiOutlineShoppingCart className="fs-1 btn-fontawsome " />
          </Button> */}
        </div>
      </div>
    </div>
    </div>
  )
}
