import React from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import '../App.css';
import { NavLink } from 'react-router-dom';
import logo from "../../src/components/Assests/image/logo.png"

export const Header = () => {
  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container>
        <NavLink className='fs-2 fw-bold  w-25 h-25' to="/">
        <img src={logo} alt='loo'width="30%" />
        </NavLink>
        <Navbar.Toggle aria-controls="navbarScroll"/>
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="mx-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <NavLink  className='fs-5  fw-700 text-dark  ms-5 navsec' to="/">Home</NavLink>
            <NavLink className='fs-5  fw-700 text-dark  ms-5 navsec' to="/shop">Shop</NavLink>
            <NavLink  className='fs-5  fw-700 text-dark  ms-5 navsec' to="/about">About</NavLink>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}


