import React, { useEffect, useState, useRef } from "react";

// import { BsArrowUpLeft } from "react-icons/bs";
import { IoIosArrowForward, IoIosArrowUp } from "react-icons/io";
import Box from "@mui/material/Box";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import { ProductCard } from "../components/ProductCard";
import {
  Container,
  Row,
  Col,
  Image,
  Button,
  Pagination,
} from "react-bootstrap";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";
import ClickAwayListener from "@mui/material/ClickAwayListener";
import { ButtonGroup } from "react-bootstrap";
import data from "../utlis/data.json";
import utils from "../utlis/utlis";

const Items = ({ item, handleChange }) => {
  return (
    <FormControlLabel
      className="text-dark"
      key={`item-${item.id}`}
      label={item.name}
      control={<Checkbox onClick={(e) => handleChange(e, item.name)} />}
    />
  );
};

export const Sevices = () => {
  const [categories, setCategories] = useState([]);
  const [products, setProducts] = useState([]);
  const [allProducts, setAllProducts] = useState([]);
  const [brands, setBrands] = useState([]);
  const [filters, setFilter] = useState([]);
  const [selectedIndex, setSelectedIndex] = useState(1);
  const anchorRef = useRef(null);
  const [open, setOpen] = useState(false);
  const [selectedBrands, setSelectedBrands] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [count, setCount] = useState(0);
  const [active, setActive] = useState(1);
  const [pageView, setPageView] = useState(9);
  const [itemPage, setItemPage] = useState(pageView);
  let pageItem = [];

  for (let num = 1; num <= count; num++) {
    pageItem.push(
      <Pagination.Item
        key={num}
        active={num === active}
        onClick={() => setActivePage(num)}
      >
        {num}
      </Pagination.Item>
    );
  }

  const setActivePage = (number) => {
    console.log(number);
    const total = parseInt(pageView) * parseInt(number);
    setItemPage(total);
    setActive(number);
  };

  const getCategories = () => {
    setCategories(data.category);
  };
  const getBrands = () => {
    setBrands(data.brands);
  };

  const getProducts = () => {
    setProducts(data.product);
    setAllProducts(data.product);
    const number =
      data.product.length / pageView + (data.product.length % 2 != 0 ? 1 : 0);
    setCount(number);
  };

  const getFilter = () => {
    setFilter(data.filters);
  };
  useEffect(() => {
    getCategories();
    getBrands();
    getProducts();
    getFilter();
  }, []);
  // fetch categories list
  const onSearchCategory = (value) => {
    var brandSeled = selectedBrands;
    var filteredProducts = allProducts;
    filteredProducts = utils.filterArray(allProducts, "category", value);

    filteredProducts = brandSeled.length
      ? filteredProducts.filter((product) => brandSeled.includes(product.brand))
      : filteredProducts;

    setProducts(filteredProducts);
    setSelectedCategory(value);
  };

  const handleMenuItemClick = (index) => {
    setSelectedIndex(index);
  };

  const handleChange = (event, brand) => {
    const isChecked = event.target.checked;
    var brandSeled = selectedBrands;
    var filteredProducts = allProducts;
    if (isChecked) {
      brandSeled.push(brand);
    } else {
      brandSeled = brandSeled.filter((Seled) => Seled !== brand);
    }

    filteredProducts =
      selectedCategory != ""
        ? utils.filterArray(allProducts, "category", selectedCategory)
        : allProducts;

    filteredProducts = brandSeled.length
      ? filteredProducts.filter((product) => brandSeled.includes(product.brand))
      : filteredProducts;

    setProducts(filteredProducts);
    setSelectedBrands(brandSeled);
  };

  // const productDetails = (id) => {
  //   navigate("/product-details/"+id);
  // }

  return (
    <div>
      <div className="shop-section mb-5">
        <div className="shop-header-section ">
          <h3 className="text-center fs-1 fw-bold  my-5"> Shop</h3>
        </div>

        <div className="container">
          <div className="row">
            <div className="col-lg-3  text-left ">
              <div className="row d-flex my-4">
                <div
                  className="w-100 rounded"
                  style={{ backgroundColor: "#f5f5fa" }}
                >
                  <h3 className="px-3 py-3 text-dark">Categories</h3>
                  <ul
                    style={{
                      listStyle: "none",
                      textAlign: "left",
                      color: "black",
                    }}
                  >
                    {categories.map((category) => (
                      <li
                        role="button"
                        className="my-2"
                        onClick={(e) => onSearchCategory(category.name)}
                        key={category.id}
                      >
                        {category.name} <IoIosArrowForward />
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
              <div className="row ">
                <div
                  className="w-100 py-3 rounded "
                  style={{ backgroundColor: "#f5f5fa", color: "text" }}
                >
                  <h3 className="px-3 py-2"></h3>
                  <div className="text-left px-1">
                    <Box
                      sx={{ display: "flex", flexDirection: "column", ml: 3 }}
                    >
                      <FormControlLabel
                        className="text-dark"
                        label="All"
                        control={<Checkbox onChange="" />}
                      />
                      {brands.map((item, index) => (
                        <Items
                          key={index}
                          item={item}
                          handleChange={handleChange}
                        />
                      ))}
                    </Box>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-9  rounded">
              <div className="rounded my-2">
                <Container fluid>
                  <div className="row">
                    <div
                      className="d-flex justify-content-end  rounded-pills p-2 "
                      style={{
                        backgroundColor: "#d0d9df005",
                        borderradius: "14px",
                      }}
                    >
                      {/* <DropdownButton
                        className="bg-white "
                        as={ButtonGroup}
                        variant="contained"
                        ref={anchorRef}
                        title={filters[selectedIndex]}
                      >
                        {filters.map((option, index) => (
                          <Dropdown.Item
                            key={option}
                            active={index === selectedIndex}
                            onClick={() => handleMenuItemClick(index)}
                          >
                            {option}
                          </Dropdown.Item>
                        ))}
                      </DropdownButton> */}
                      {open && (
                        <div
                          className="position-absolute "
                          style={{ zIndex: 1 }}
                        >
                          <div className="bg-white border">
                            <ClickAwayListener
                              onClickAway={() => setOpen(false)}
                            >
                              <ul className="list-unstyled mb-0">
                                {filters.map((option, index) => (
                                  <li key={option}>
                                    <button
                                      className={`dropdown-item ${
                                        index === selectedIndex ? "active" : ""
                                      }`}
                                      onClick={() => handleMenuItemClick(index)}
                                    >
                                      {option}
                                    </button>
                                  </li>
                                ))}
                              </ul>
                            </ClickAwayListener>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>

                  <Row className="">
                    {products.map((product, index) => {
                      if (index < itemPage) {
                        return (
                          <Col lg={4} md={4} key={index} className="mb-4">
                          <ProductCard product={product} index={index}/>
                        </Col>
                        )
                      }
                    })}
                  </Row>
                  <Pagination>{pageItem}</Pagination>
                </Container>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
