import React from "react";
import '../App.css';
import AboutImg from '../components/Assests/image/aboutImg.jpg';
export const About = () => {
    return (
        <div className=" bg-light mb-5">
                <div class="container" style={{FontFamily:'nexa sensserrif',}}>
    <div class="row justify-content-center   align-items-center">
      <div class=" col-lg-5 col-md-5 ">
        <div class="section3Text">
          <h3 style={{fontSize:"35px"}} class="fw-bold text-dark">About Us</h3>
          <p class="mt-3 text-dark aboutPara">
            Overseas medical education offers promising prospects, but it comes with hurdles, especially in prestigious courses like MBBS, MD, and MS. These obstacles encompass seat shortages due to high demand, language disparities, distinct study patterns emphasizing quality and practical learning, and increased expenses driven by currency differences. Adapting to these challenges is crucial for success in foreign medical studies.
          </p>
          {/* <!-- <a class="btn_2" href="#">learn more</a> --> */}
        </div>
      </div>
      <div class="col-md-6 col-lg-6 mb-3 ">
        <div class="about_us_img">
        <img src={AboutImg} alt= "aboutImg" className="w-75 rounded "/>
        </div>
      </div>

    </div>
  </div>
        </div>
   
     
    );
}